import React, { useCallback } from 'react';
import cn from 'classnames';
import Button from 'components/Button';
import { PageHeader } from 'components/Page';
import { grid, gridColumn, gridItem, gridItemAlignCenter } from '@itv/fe-components/cjs/common/styles/grid.cls';
import { PageTypes, PageIds } from 'utils/tracking';
import { useNavActions } from 'contexts/NavContext';
import { useParentalControls, parentalControlsStatus, parentalControlsManagerType } from 'contexts/ParentalControlsContext';
import { ParentalControlsDialogs } from 'components/ParentalControls';
import { useAppEventsContext } from 'contexts/AppEventsContext';
import SettingsPage from '../SettingsPage';
import './ParentalControls.scss';

const { ON, OFF } = parentalControlsStatus;

const ParentalControls = () => {
    const {
        state: { status, defaultManager },
        actions: { displayCreatePin, displayExistingPin }
    } = useParentalControls();
    const {
        state: { closePlayerChecks }
    } = useAppEventsContext();

    const displayNextDialog = useCallback(
        () => (status === OFF ? displayCreatePin() : displayExistingPin()),
        [displayCreatePin, displayExistingPin, status]
    );

    const { setSubNavStealFocus } = useNavActions();

    const onCloseDialogs = useCallback(() => setSubNavStealFocus(true), [setSubNavStealFocus]);

    const parentalControlsContentMap = {
        [parentalControlsManagerType.YOUVIEW]: (
            <>
                <p>If you&apos;d like to use Parental Controls, go to YouView Settings and select Parental Controls.</p>
                <p>Here you will be able to turn Parental Controls on or off and manage your PIN.</p>
            </>
        ),
        [parentalControlsManagerType.YOUVIEW_SONY]: (
            <>
                <p>To turn on, turn off or manage Parental Controls, please follow these instructions:</p>
                <p>Exit the ITVX app and press the Home button on your remote.</p>
                <p>Navigate to your TV settings and then select Parental Lock and follow the onscreen instructions.</p>
            </>
        ),
        [parentalControlsManagerType.DEFAULT]: (
            <>
                <div className="ParentalControls__state">
                    Parental controls are&nbsp;
                    <span data-testid="parental-controls-status" className={`ParentalControls__state--${status.toLowerCase()}`}>
                        {status}
                    </span>
                </div>
                <div className="ParentalControls__subtitle">
                    <p className="ParentalControls__subtitle__item">
                        Activating parental controls restricts access to potentially unsuitable videos.
                        <br />
                        <br />
                        For more information please read our FAQ&apos;s available at{' '}
                        <strong className="ParentalControls__subtitle__item--strong">www.itv.com/guidance</strong>
                    </p>
                </div>
                <Button
                    id="parental-controls-switch"
                    className="ParentalControls__button"
                    onClick={displayNextDialog}
                    backTo="parental-controls-link"
                >
                    {`Turn ${(status === OFF ? ON : OFF).toLowerCase()}`}
                </Button>
            </>
        )
    };

    return (
        <>
            <SettingsPage id="parentalControlsPage" trackingPageType={PageTypes.ACCOUNT} trackingPageId={PageIds.PARENTAL_CONTROLS}>
                <div className={cn('ParentalControls', grid, gridColumn)}>
                    <div className={cn(gridItem, gridItemAlignCenter, 'ParentalControls__container')}>
                        <PageHeader title="Parental Controls" headerClassName="ParentalControls__header" />
                        {parentalControlsContentMap[defaultManager]}
                    </div>
                </div>
            </SettingsPage>
            {!closePlayerChecks && <ParentalControlsDialogs onCancel={onCloseDialogs} onConfirm={onCloseDialogs} />}
        </>
    );
};

export default ParentalControls;
